import React, { useState, useEffect } from 'react';
import './navbar.scss';
import logo from './logo.svg';
import { Link } from 'react-router-dom';
import SlideNav from '../slide-nav/slide-nav';

const Navbar = ({bg}) => {
        const [isVisible, setIsVisible] = useState(false);

        const toggleNav = () => {
            setIsVisible(prev => !prev);
          };
          
          useEffect(() => {
            return () => null
          }, [])

    
        return (
        <div className="navbar" style={{backgroundColor: bg}}>
            <Link to='/'><img src={logo} alt="logo" /></Link>
            <div className="links">
                <ul>
                    <Link style={{textDecoration: 'none'}} to='#'><li>Live streams</li></Link>
                    <li>Restaurants near you</li>
                    <Link style={{textDecoration: 'none'}} to='#'><li>Add your restaurant</li></Link>
                    <Link to='signin'><li id='sign-in'>Sign In</li></Link>
                    <Link to='#'><li id='sign-up'>Sign Up</li></Link>
                </ul>
            </div>
            <div className="menux" onClick={toggleNav}>
                <div className="one line"></div>
                <div className="two line"></div>
                <div className="three line"></div>
            </div>
            {isVisible && <SlideNav toggleNav={toggleNav} />}

        </div>
    )
    
}

export default Navbar;