export const TO_VERIFY = 'to_verify';
export const SET_USER = 'set_user';
export const SET_MENU = 'set_menu';

export const setVerifyEmail = payload => {
    return {
        type : TO_VERIFY,
        payload
    }
}

export const setUser = payload => {
    return {
        type : SET_USER,
        payload
    }
}

export const setMenu = payload => {
    return {
        type : SET_MENU,
        payload
    }
}

export const setNewMenu = ({menu, description, price, image}) => async (dispatch) => {
    try {
        const token = window.localStorage.getItem("__token");
        const formData = new FormData();
        formData.append('description',description);
        formData.append('price',Number(price));
        formData.append('description',image);
        const res_json  = await fetch(`https://streameats.com.ng/api/menu`,{headers:{'content-type':'multipart/form-data;','Authorization' : `Bearer ${token}`}});
        const res = await res_json.json();
        if(!res.ok){
            throw new Error(res_json.message);
        }
        setMenu([...menu,{description,price,image}]);
    } catch (error) {
        console.log(error.message);   
    }
};