import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {setVerifyEmail} from "../../../redux/actions/user";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './hero.scss';

const Hero = props => {

    const initialState = {
        business_name : '',
        phone : '',
        email : '',
        first_name : '',
        last_name : '',
        address : ''
    }

    const [details, setDetails] = useState(initialState);
    const dispatch = useDispatch();
 
    const signUp = async () => {
        try {
            
            const data = JSON.stringify(details);
            const res_json  = await fetch('https://streameats.com.ng/api/user',{method : 'POST',headers:{'content-type':'application/json'},body:data});
            const res = await res_json.json();
            if(!res_json.ok){
                return toast.error(res.message,{
                    position : toast.POSITION.TOP_RIGHT
                })
            }
            dispatch(setVerifyEmail({verify_email : details.email, verify_from : 'register'}));
            props.history.push('/verify');
        } catch (error) {
            return toast.error(error.message,{
                position : toast.POSITION.TOP_RIGHT
            })
        }
    }

    return (
        <>
            <div className="rest-hero">
            <div className="content">
                <div className="left">
                        <form>
                            <div className="title">Get Started</div>
                            <input type="text" placeholder='Restaurant name' value={details.business_name} onChange={e => setDetails({...details,business_name:e.target.value})} /><br/>
                            <input type="text" placeholder='Address' value={details.address} onChange={e => setDetails({...details, address : e.target.value })} /><br/>
                            <input type="tel" placeholder='Phone Number' value={details.phone} onChange={e => setDetails({...details,phone:e.target.value})} /><br/>
                            <input type="text" id='spc' className='small' placeholder='First name' value={details.first_name} onChange={e => setDetails({...details,first_name:e.target.value})} />
                            <input type="text" className='small' placeholder='Last name' value={details.last_name} onChange={e => setDetails({...details,last_name:e.target.value})} /><br/>
                            <input type="email" placeholder='Email' value={details.email} onChange={e => setDetails({...details,email:e.target.value})} /><br/>
                            <button type='button' onClick={signUp}>Finish</button>
                        </form>
                </div>
                <div className="right">
                        <h1>Boost your restaurant business with livestream</h1>
                        <p>Stream Eats platform lets you give your customers the best experience combining online orders and livestream. <br/> <br/> Utilize our vast network of delivery services automatically managed by the customer to get those meals moving round the clock. <br/> <br/> Our livestream feature helps support start-up home restaurants by allowing you provide real-time insights on your customer order to boost edible confidence. <br/> <br/> Boost revenue as a home restaurant start-up by providing 24/7 services.</p>
                </div>
            </div>
            </div>
            <ToastContainer />
        </>
    )
}

export default withRouter(Hero);