import React, {useState} from "react";
import {useDispatch} from 'react-redux'
import { Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import logo from './logo.svg';
import "./sign-in.scss";
import { setVerifyEmail } from "../../redux/actions/user";

const SignIn = props => {
    const [email, setEmail] = useState('');
    // const [loading, setLoading] = useState(false);
    const dispatch = useDispatch();

    const signIn = async () => {
        try{
            const data = JSON.stringify({email});

            const res_json  = await fetch('https://streameats.com.ng/api/auth/sign_in',{method : 'POST',headers:{'content-type':'application/json'},body:data});
            const res = await res_json.json();

            if(!res_json.ok){
                return toast.error(res.message,{
                    position : toast.POSITION.TOP_RIGHT
                });
            }

            dispatch(setVerifyEmail({verify_email : email, verify_from : 'login'}));
            props.history.push('/verify');

        } catch (error) {
            toast.error('Unable to Sign In',{
                position : toast.POSITION.TOP_RIGHT
            });
        }
    }


    return (
        <>
            <div className="sign-in">
                    <Link to='/'><img src={logo} alt="logo"/></Link>
            
                <div className="content">
                    <h1>Awesome, your back</h1>

                    <div className="info">
                    {/* <p>Sign in to place your order (customers)*</p> */}
                    <p>Sign in to access your restaurant</p>
                    </div>

                    <form>
                        <input type="text" id='number' placeholder='Email Address' value={email} onChange={e => setEmail(e.target.value)} /> <br/>
                        <button type='button' onClick={signIn}>Next</button>
                        <p>New to Stream Eats? <Link to='/'><span id='create'>Create an account</span></Link></p>
                    </form>
                </div>

                <div className="footer">
                    <div className="cont">
                        <div className="left">2021 Stream Eats, Inc.</div>
                        <div className="right">
                            <ul>
                                <li>Privacy Policy</li>
                                <li>Terms of Use</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        <ToastContainer />
        </>
    )
}

export default SignIn;
