import React, { useState } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { Redirect, Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';

import 'react-toastify/dist/ReactToastify.css';
import "./verify.scss";
import logo from './logo.svg';
import {  setUser } from "../../redux/actions/user";

const Verify = props => {

    const [code, setCode] = useState('');
    
    const user = useSelector(state => state.user);
    const verifyFrom = user.verify_from;
    const email = user.verify_email;

    const dispatch = useDispatch();

    const setCurrentUser = async token  => {
        try {
            const res  = await fetch(`https://streameats.com.ng/api/user`,{headers : {Authorization : `Bearer ${token}`}});
            
            const res_json = await res.json();
    
            if(!res.ok){
                throw new Error("I cannot log you in");
            }

            dispatch(setUser({restaurant:res_json.restaurant, menu: res_json.menus}));  
    
        } catch (error) {
            console.log(error.message);
        }
    }

    const verifyCode = async () => {
        try{

            const data = verifyFrom === 'register' ? JSON.stringify({verification_code : code, email}) : JSON.stringify({login_code : code, email});

            const url = verifyFrom === 'register' ? 'https://streameats.com.ng/api/auth/verify_sign_up' : 'https://streameats.com.ng/api/auth/verify_sign_in';

            const res  = await fetch(url,{method : 'POST',headers:{'content-type':'application/json'},body:data});
            
            const res_json = await res.json();

            if(!res.ok){
                return toast.error(res.message,{
                    position : toast.POSITION.TOP_RIGHT
                })
            }

            window.localStorage.setItem("__token",res_json.data);

            const token = res_json.data;
            
            await setCurrentUser(token);

            props.history.push('/edit-profile');
        } catch (error) {
            console.log(error.message);
            toast.error('Unable to sign In',{
                position : toast.POSITION.TOP_RIGHT
            })
        }
    }

    return (
        <>
            {
                email === '' ? (
                    <Redirect to="/" />
                ) : (
                    <>
                        <div className="verify">
                            <Link to='/'><img src={logo} alt="logo"/></Link>
                            <div className="content">
                                <h1>Almost there</h1>
                                <form>
                                    <label htmlFor="number">Enter verification code sent to {email}</label><br/>
                                    <input type="text" id='number' placeholder='Verification code' value={code} onChange={e => setCode(e.target.value)} /> <br/>
                                    <button type='button' onClick={verifyCode}>Finish</button>
                                </form>
                            </div>
                            <div className="footer">
                                <div className="cont">
                                    <div className="left">2021 Stream Eats, Inc.</div>
                                    <div className="right">
                                        <ul>
                                            <li>Privacy Policy</li>
                                            <li>Terms of Use</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <ToastContainer />
                    </>
                )
            }
        </>
    )    

}

export default Verify;
