import React from 'react';
import Footer from './components/footer/footer';
import Navbar from './components/navbar/navbar';
import Faqs from './components/Rest-sign-up/faqs/faqs';
import Hero from './components/Rest-sign-up/hero/hero';
import Review from './components/Rest-sign-up/review/review';
import StartSteps from './components/Rest-sign-up/start-steps/start-steps';

const RestSignUp = () => {
    const bg = '#191735'
    return (
        
        <div className='rest-sign-up'>
           <Navbar bg={bg} />
           <Hero />
           <Review />
           <StartSteps />
           <Faqs />
           <Footer />
        </div>
    )
}

export default RestSignUp;