import React, { useRef, useState } from "react";
import { useSelector } from 'react-redux';
import { Redirect } from 'react-router-dom';
import "./edit-profile.scss";
import headerImg from "../../assets/profile/header.png";
import Goodeats from '../../assets/goodeats.svg';
import Navbar from "../navbar/navbar";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { setMenu } from "../../redux/actions/user";

const EditProfile = props => {
  const [showMenu, setShowMenu] = useState(false);
  const noOfOrders = "0";

  // refs
  const foodNameRef = useRef("");
  const foodPriceRef = useRef("");
  const foodImageRef = useRef("");
  const userNumberRef = useRef("");

  // selector and defaults
  const restaurant = useSelector(state => state.user);
  const profile = restaurant.user;
  const menuList = restaurant.menu;
  const token = window.localStorage.getItem("__token");
  const isLive = false;

  const getInfo = async (message) =>{
    toast.info(message,{
      position : toast.POSITION.TOP_RIGHT
  });
  }

  const addMenuItem = async (e) => {
    try {
      e.preventDefault();
      const token = window.localStorage.getItem("__token");
        const formData = new FormData();
        formData.append('description',foodNameRef.current.value);
        formData.append('price',Number(foodPriceRef.current.value));
        formData.append('image',foodImageRef.current.files[0]);
        const res  = await fetch(`https://streameats.com.ng/api/menu`,{method:'POST',body:formData,headers:{'content-type':'multipart/form-data;','Authorization' : `Bearer ${token}`}});
        const res_json = await res.json();
        console.log(res_json);
        if(!res.ok){
            throw new Error(res_json.message);
        }
        toast.success("Item added successfully",{
          position : toast.POSITION.TOP_RIGHT
      });
        setMenu([...menuList,{description : foodNameRef.current.value,price : foodPriceRef.current.value,image : foodImageRef.current.files[0]}]);
    } catch (error) {
      toast.error(error.message,{
        position : toast.POSITION.TOP_RIGHT
    });
    }
  };

  const removeRestaurantMenuItem = async (menuId) => {
    try {
      console.log(menuId);
      const res  = await fetch(`https://streameats.com.ng/api/menu/${menuId}`,{method : 'Delete',headers:{'content-type':'application/json',Authorization : `Bearer ${token}`}});
      const res_json = await res.json();
        if(!res.ok){
            throw new Error('Unable to delete Item');
        }
        toast.success(res_json.message,{
          position : toast.POSITION.TOP_RIGHT
      });
        const newMenu = menuList.filter(item => item._id != menuId);
        setMenu(newMenu);
    } catch (error) {
      toast.error(error.message,{
        position : toast.POSITION.TOP_RIGHT
    });
    }
  }

  const copyLink = () =>{
    const elem = document.body.appendChild(document.createElement('input'));
      elem.value = profile.streameats_link;
      elem.focus();
      elem.select();
      elem.setSelectionRange(0,99999);
      document.execCommand('copy');
      elem.parentNode.removeChild(elem);
      toast.success('Copied to ClickBoard',{
          position : toast.POSITION.TOP_RIGHT
      })
  };
  
  // useEffect(() => {
  //   getRestaurantMenu();
  //   // eslint-disable-next-line
  // }, []);

  return (
    <>
    {
    restaurant.is_auth ? (
      <div className="edit-profile">
    <Navbar bg="#191735"/>
    <div
      className="h-40 md:h-52 lg:h-64"
      style={{
        backgroundImage: `url(${headerImg})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
      }}
    ></div>
    <div className="flex items-center justify-between px-4 md:px-8 lg:px-16 xl:px-24 py-4">
      <div className="flex items-end">
        {   
          isLive ? (
              <div className=" h-20 w-20 lg:h-32 lg:w-32 border bg-gray-100 mr-2 lg:mr-6 rounded-full bg-contain flex items-end justify-center border-red-400">
                  <span className='px-2 py-1 border border-black-400 rounded-lg bg-red-400 relative top-4 text-1 text-white'>Live</span>
              </div>
          ) : (
              <div className=" h-20 w-20 lg:h-32 lg:w-32 border bg-gray-100 mr-2 lg:mr-6 rounded-full bg-contain flex items-end justify-center" style={{backgroundImage:Goodeats}}></div>
          )
        }
        <div className="relative bottom-2">
          <p className="font-bold lg:text-6 mb-2">{profile.business_name}</p>
          <button
            style={{ outline: "none" }}
            className="px-6 py-2 border border-black-400 rounded-lg"
            onClick={() => copyLink()}
          >
            Copy Link
          </button>
        </div>
      </div>
      <div className="actions text-right">
        <button
          className="w-40 h-10 items center text-center rounded-xl text-white text-1.5 mb-2"
          onClick={() => getInfo("You cannot go live till we officially Launch")}
          style={{ backgroundColor: "#F44343", outline: "none" }}
        >
          Go live
        </button>
        <button
          className="w-full h-10 items center text-center rounded-xl text-white text-1.5"
          style={{ backgroundColor: "#FF7948", outline: "none" }}
        >
          Change cover photo
        </button>
      </div>
    </div>
    <div className=" px-4 md:px-8 lg:px-16 xl:px-24 mb-10">
      <h3 className="text-2 lg:text-3 font-bold mb-2">{profile.bio}</h3>
      <p className="text-1.5 lg:text-2.5 mb-6">
        Streamed over <span className="font-bold">{noOfOrders}</span> orders
      </p>
    </div>



    <div className="prof">
      <div className="header" onClick={() => setShowMenu(!showMenu)}>
        <p>Update profile</p>
        <i className="fas fa-angle-down" id={showMenu ? "rotated" : null}></i>
      </div>

      {showMenu ? (
        <div>
          <input
            type="text"
            placeholder="Restaurant name"
            name="restName"
          />{" "}
          <br />
          <input
            type="text"
            placeholder="Bio"
            name="bio"
          />{" "}
          <br />
          <input type="tel" placeholder="Phone number" ref={userNumberRef} value={profile.phone} /> <br />
          <button>save</button>
        </div>
      ) : null}

      <div className="menuxx">
        <div className="header">
          <p className="titlex">Menu</p>
          <p className="titlex">Price</p>
        </div>

        <div className="add-menu">
          <form>
            <div className="top">
              <input type="text" id="name" placeholder="Type food name" ref={foodNameRef} />
              <input type="tel" id="price" placeholder="price" ref={foodPriceRef} />
            </div>
            <div className="actions">
              <input type="file" id="file" ref={foodImageRef} />
              <input type="submit" id="submit" onClick={ e => addMenuItem(e)} />
            </div>
          </form>
        </div>

        <div className="menu-list">
          {
            menuList.length === 0 ? (
              <div className="flex justify-center h-full items-center ">
                <p className="text-1.5 lg:text-3 text-center font-bold">This Restaurant currently have no menu <br></br>stay tuned!</p>
              </div>
            ) : (
              menuList.map(({ _id :id, description, price }) => {
                return (
                  <div className="item" key={id}>
                      <p className="food">{description}</p>
                      <p className="price">
                          <span className="mr-4">N{price}</span>
                          <span>
                              <button type="button" value={id} style={{background : "none"}} className='cursor-pointer' onClick={(e) => removeRestaurantMenuItem(e.target.value)}>X</button>
                          </span>
                      </p>
                  </div>
                );
              })
            )
          }
        </div>
      </div>

      <div className="paymentx">
        <div className="header" onClick={() => setShowMenu(!showMenu)}>
          <span>Payment details.</span>
          <span id="small">Secured by AccessPay</span>
        </div>
        

        <form>
          <input type="tel" placeholder="Bank Acoount" /> <br />
          <input type="" placeholder="Bank Name" /> <br />
          {/* <input type="tel" placeholder="Security code / CVC" /> */}
          <button>
            Add Account Details To Receive Payment
          </button>
          {/* <p id="ps-txt">secured by paystack</p> */}
        </form>
      </div>
    </div>
    <ToastContainer />
  </div>
      ) : (
        <Redirect to='/' />
      )
    }
    </>
  );
};

export default EditProfile;
