import React from 'react';
import './start-steps.scss';
import steps from './steps.png';

const StartSteps = () => {
    return (
        <div className="start-steps">
            <div className="content">
                <div className="left">
                   <img src={steps} alt="steps"/>
                   <button>Join us</button>
                </div>
                <div className="right">
                    <p className="title">Start-up in just 3 steps</p>
                    <ul>
                        <li>1. Tell us about your restaurant</li>
                        <li>2. Upload a menu</li>
                        <li>3. Start receiving orders</li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default StartSteps;