import { TO_VERIFY, SET_USER, SET_MENU } from '../actions/user';

const initialState = {
    user : {
    },
    menu : {
    },
    verify_email : '',
    verify_from : '',
    is_auth : false
}

const userReducer = (state= initialState, {type,payload}) => {
    switch ( type ) {
        case TO_VERIFY:
            return {
                ...state,
                verify_email : payload.verify_email,
                verify_from : payload.verify_from
            }
        case SET_USER:
            return {
                ...state,
                is_auth : true,
                user : payload.restaurant,
                menu : payload.menu
            }
        case SET_MENU:
            return {
                ...state,
                menu:payload
            }
        default:
            return state
    }
}

export default userReducer;